import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class ModelsService {
  serverBaseUrl: string = environment.serverBaseUrl;
  formData: FormData = new FormData();
  constructor(private _http: HttpClient) { }

  /**
   * Fetches all the models list of particular training
   * @param projectId
   * @returns
   */
  getModelsList(trainingId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/training/${trainingId}/models/list`);
  }

  /**
   * Fetches all the trainings list
   * @param projectId
   * @returns
   */
  getTrainingList(projectId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/projects/${projectId}/training/list`);
  }

  /**
   * Deletes the model
   * @param projectId
   * @param dataId
   */
  deleteModel(trainingId: string, modelId: string): Observable<any> {
    return this._http.delete(`${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}`);
  }

  /**
   * Generates the decision tree
   * @param modelId
   * @param reqObj
   * @returns
   */
  getDecisionTree(modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/models/${modelId}/graph/decision_tree`,
      reqObj
    );
  }

  /**
   * Fetches the analysis report
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  getAnalysisReport(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/analysis_report/report`,
      reqObj
    );
  }

  /**
   * Evalues the forecast graph
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  evaluateGraph(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/timeseries/evaluate_forecast`,
      reqObj
    );
  }

  /**
   * Generates the classification report
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  getClassificationReport(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/report/classification`,
      reqObj
    );
  }

  /**
   * Generates the cluster visualization graph
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  visualiseGraph(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/clustering/visualize`,
      reqObj
    );
  }
  /**
   * Generates the lime report
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  getLimeReport(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/report/lime_report`,
      reqObj
    );
  }

  /**
   * Generates the forcast graph
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  getForecastGraph(trainingId: string, modelId: string, reqObj: any) {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/timeseries/forecast`,
      reqObj
    );
  }

  /**
   * Generates the regression line plot
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  getRegressionLinePlot(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/rl_plot/read`,
      reqObj
    );
  }

  /**
   *  Generates the ROC-AUC curve plot
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  getRocAucPlot(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/graph/rocauc`,
      reqObj
    );
  }

  /**
   *  Generates the lift-gain curve plot
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  getLiftGainPlot(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/graph/liftgain?algoName=${reqObj.algoName}`,
      reqObj
    );
  }

  /**
   *  Generates the PR curve plot
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  getPrCurvePlot(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/graph/prcurve`,
      reqObj
    );
  }

  /**
   * Performs the hold out
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  performHoldOut(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/holdout`,
      reqObj
    );
  }

  /**
   *  Generates the confusion matrix plot
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  getConfusionMatrixPlot(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/cm/read`,
      reqObj
    );
  }

  /**
   * Performs the scoring result
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  performScoring(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/scoring/do`,
      reqObj
    );
  }

  /**
   * Generates the learning curve graph
   * @param trainingId
   * @param modelId
   * @param algoName
   * @param reqObj
   * @returns
   */
  getLearningCurvePlot(
    trainingId: string,
    modelId: string,
    algoName: string,
    reqObj: any
  ): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/graph/learning_curve?algoName=${algoName}`,
      reqObj
    );
  }

  /**
   * Generates the feature impact graph
   * @param modelId
   * @param iterations
   * @returns
   */
  getFeatureImpact(modelId: string, iterations: number, tab: string): Observable<any> {
    return this._http.get(
      `${this.serverBaseUrl}/api/models/${modelId}/featureImpact?iterations=${iterations}&tab=${tab}`
    );
  }

  /**
   * Calculates the CV score
   * @param trainingId
   * @param modelId
   * @param algoName
   * @param algoType
   * @param status
   * @returns
   */
  runCv(
    trainingId: string,
    modelId: string,
    algoName: string,
    algoType: string,
    validationStartegytechnique: string,
    validationStartegynSplit: number,
    metric: any,
    edaId: any,
    status: string
  ): Observable<any> {
    return this._http.get(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/cv?algoName=${algoName}&algoType=${algoType}&validationStartegytechnique=${validationStartegytechnique}&validationStartegynSplit=${validationStartegynSplit}&metric=${metric}&edaId=${edaId}&cvStatus=${status}`,
      { responseType: 'text' }
    );
  }

  /**
   * Generates the feature distribution graph
   * @param modelId
   * @param iterations
   * @returns
   */
  getDistributionGraph(
    modelId: string,
    reqObj: any
  ): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/models/${modelId}/featureDistribution`,
      reqObj
    );
    // return this._http.post(
    //   `${this.serverBaseUrl}/api/models/${modelId}/featureDistribution?bins=${bins}&featureName=${featureName}&targetClass=${target}`
    // );
  }

  /**
   * Generates the form fields for QP
   * @param trainingId
   * @param modelId
   * @param projectType
   * @param reqObj
   * @returns
   */
  getQuickPredictionFormData(
    trainingId: string,
    modelId: string,
    projectType: string,
    reqObj: any
  ): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/qp/populate_form_data?projectType=${projectType}`,
      reqObj
    );
  }

  /**
   * Generates the prediction result preview table
   * @param predictionId
   * @returns
   */
  generatePreviewTable(predictionId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/prediction/${predictionId}/read`);
  }

  /**
   * Ecxecutes the quick perdiction
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  doPrediction(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/doprediction`,
      reqObj
    );
  }

  /**
   * Uploads the file and do quick prediction
   * @param file project file details
   */
  fileUploadPrediction(file: any, projectId: string, fileEncoding: any): Observable<any> {
    this.formData.set('file', file, file.name);
    return this._http.post(
      `${this.serverBaseUrl}/api/projects/${projectId}/data?type=prediction&fileEncoding=${fileEncoding}`,
      this.formData
    );
  }

  /**
   * Gets Pipeline Task
   * @param trainingId
   * @returns
   */
  getPipelineTask(trainingId: string): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/training/${trainingId}/get_pipelinetasks`);
  }

  /**
   * Pipeline Details Task
   * @param trainingId
   * @param reqObj
   * @returns
   */
  getPipelineDetails(trainingId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/get_pipelinetask/details`,
      reqObj
    );
  }

  /**
   *Uploads the scoring data
   * @param file
   * @param trainingId
   * @returns
   */

  uploadScoringData(file: any, trainingId: string, fileEncoding: any): Observable<any> {
    this.formData.set('file', file, file.name);
    return this._http.post(
      `${environment.serverBaseUrl}/api/training/${trainingId}/scoring/data?fileEncoding=${fileEncoding}`,
      this.formData
    );
  }

  /**
   * Fetches all the hosts list
   * @param projectId
   * @returns
   */
  getHostList(): Observable<any> {
    return this._http.get(`${this.serverBaseUrl}/api/settings/general`);
  }

  /**
   * Fetches all the hosts list
   * @param projectId
   * @returns
   */
  preDeployModel(obj: any, selectedHost: any): Observable<any> {
    return this._http.get(
      `${this.serverBaseUrl}/api/training/${obj.training._id}/models/${obj.modelInfo._id}/deploy/${selectedHost._id}?ag=${obj.modelInfo.modelMetaData.algoName}&algoType=${obj.algoType}&pf=${obj.platform}&projectType=${obj.project.type}&projectName=${obj.project.name}&reTrainFileId=${obj.fileId}&operation=model-deployment&notebooks=${obj.notebooks}&notebookInputFileId=${obj.notebookInputFileId}&psType=${obj.psType}`
    );
  }

  /**
   * Function to deploy model post
   * @param reqObj
   * @returns 
   */
  deployModelPost(reqObj: any): Observable<any> {
    return this._http.post(`${this.serverBaseUrl}/api/models/insertDeployment`, reqObj);
  }

  /**
   * function to update the model
   * @param reqObj
   * @param modelId
   * @returns
   */
  updateModel(reqObj: any, modelId: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/models/${modelId}/update`,
      reqObj
    );
  }

  /**
   * function to get decision tree for clustering
   * @param trainingId
   * @param modelId
   * @param reqObj
   */
  clusterDecisionTree(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/clustering/decisionTree`,
      reqObj
    );
  }

  /**
   *  function to get heat map for clustering
   * @param trainingId
   * @param modelId
   * @param reqObj
   * @returns
   */
  clusterHeatMap(trainingId: string, modelId: string, reqObj: any): Observable<any> {
    return this._http.post(
      `${this.serverBaseUrl}/api/training/${trainingId}/models/${modelId}/clustering/heatMap`,
      reqObj
    );
  }
}
