  /**
   * Generates the lime report graph
   */
  showLimeReport(isRefresh?: boolean): void {
    isRefresh ? this._spinner.showSpinner() : null;
    this.limeReportReqObj = {
      fileEncoding: 'utf_8',
      algoType: this.modelInfo.modelMetaData.algorithmType,
      algoName: this.modelInfo.modelMetaData.algoName,
      type: this.dialogData.predMode,
      reTrainFileId: this.modelInfo.modelMetaData.reTrainFileId,
      edaSummary: this.training.edaId.edaSummary,
      filename: this.originalFileWithEdaDetails.filename,
      indexPath: this.training.edaId.indexPath,
      transformedDatasetPath: this.modelInfo.transformedDatasetPath,
      modelPath: this.modelInfo.modelMetaData.modelPath,
      trainPipeFilePath: this.modelInfo.trainPipeFilePath,
    };
    if (this.modelInfo.edaId !== null) {
      this.limeReportReqObj.edaId = this.modelInfo.edaId;
    } else {
      this.limeReportReqObj.edaId = this.training.edaId._id;
    }
    if (this.dialogData.predMode === 'file_upload') {
      // For file upload
      this.limeReportReqObj.predictionFile = this.dialogData.predictionFile.filepath;
      this.limeReportReqObj.rowId = this.dialogData.rowId || this.rowId;
    } else if (this.dialogData.predMode === 'input_form') {
      this.limeReportReqObj.predictionData = this.dialogData.quickPredformData;
      // For single row prediction
    } else {
      this.limeReportReqObj.tab = this.tab;
    }
    // API call
    this._models
      .getLimeReport(
        this.training._id || this.modelInfo.trainingId,
        this.modelInfo._id,
        this.limeReportReqObj
      )
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this.limeData = res;
            this.limeReport = JSON.parse(res.htmlContent);
            let graph = this.limeReport;
            let graphDiv: any = document.getElementById('limeReport');
            while (graphDiv.data && graphDiv.data.length > 0) {
              Plotly.deleteTraces(graphDiv, [0]);
            }
            graph.layout.template.layout.title.xanchor = 'center';
            graph.layout.template.layout.title.y = 0.9;
            graph.layout.template.layout.title.x = 0.5;
            graph.layout.template.layout.title.yanchor = 'top';
            Plotly.plot(graphDiv, graph.data, graph.layout);
            this.rowData = res.rowData;
            this._spinner.stopSpinner();
          } else {
            this._toaster.error('Cannot generate lime report', 'Models');
            this._spinner.stopSpinner();
          }
        },
        (err: HttpErrorResponse) => {
          this.limeReport = err;
          this._toaster.error(err.error?.message || 'Cannot generate lime report', 'Models');
          this._spinner.stopSpinner();
        }
      );
  }

  /**
   * Downloads the lime report
   */
  downloadLimeReport(): void {
    this._spinner.showSpinner();
    this.limeReportReqObj.operation = 'download';
    if (this.rowData) {
      this.limeReportReqObj.rowId = this.rowData[0].index;
    }
    // API call
    this._models
      .getLimeReport(this.training._id, this.modelInfo._id, this.limeReportReqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this._spinner.stopSpinner();
            this._window.open(
              `${environment.serverBaseUrl}/api/projects/${this.project._id}/LimeReport/download/${res.htmlContent}`,
              '_blank'
            );
          } else {
            this._toaster.error('Cannot download lime report', 'Models');
            this._spinner.stopSpinner();
          }
        },
        (err: HttpErrorResponse) => {
          this.limeReport = this._sanitizer.bypassSecurityTrustHtml(err.message);
          this._toaster.error(err.error?.message || 'Cannot download lime report', 'Models');
          this._spinner.stopSpinner();
        }
      );
  }

  /**
   * check datatype of selected feature in distribution plot
   * @param feat
   */
  onOperationSelection(feat: string): void {
    let feature: any = this.featureList.find((item) => item.colName === feat);
    if (feature.dataType === 'Decimal' || feature.dataType === 'Integer') {
      this.binsControl.setValue(20);
      this.showBin = true;
    } else {
      this.binsControl.setValue(null);
      this.showBin = false;
    }
  }

  /**
   * Reset the variables on the basis of graph type
   * @param graphType
   */
  graphChange(graphType: string): void {
    this.hideGraph = true;
    if (graphType === 'featureImpact') {
      this.binsControl.setValue(20);
      this.selectedFeature = '';
      this.targetFeature = '';
    } else {
      this.iterationsControl.setValue(5);
    }
  }

  /**
   * Downloads the generated analysis report
   */
  downloadAnalysisReport(): void {
    this._window.open(
      `${environment.serverBaseUrl}/api/models/${this.modelInfo._id}/analysisReport/download?fileName=${this.analysisReportData.arFileName}&pId=${this.project._id}`,
      '_blank'
    );
  }

