  /**
   * Renders the snackbar
   */
  showSnackBar(message: string, button: string): void {
    this.snackBarRef = this._snackBar.open(message, button, {
      horizontalPosition: 'right',
      verticalPosition: 'bottom',
    });
  }

  /**
   * Binds the object to the drop down (N Gram)
   * @param item1
   * @param item2
   * @returns
   */
  compareWithFn(item1: any, item2: any): boolean {
    return item1 && item2 ? item1.id === item2.id : item1 === item2;
  }

  /**
   * Resizes analysis report table columns width
   * @param event
   */
  gridReady(event: any) {
    // event.api.sizeColumnsToFit();
    // this.$win.on(this.resizeEvent, () => {
    //   setTimeout(() => {
    //     event.api.sizeColumnsToFit();
    //   });
    // });
    event.api.setDomLayout('autoHeight');
    this.gridApi = event.api;
    this.gridApi.refreshCells({ force: true });
    this.gridApi.setColumnDefs(this.gridOptions.columnDefs);
    this.gridApi.setRowData(this.gridOptions.rowData);
    event.columnApi.autoSizeAllColumns();
    // event.api.sizeColumnsToFit();
  }

  /**
   * Function to sort an array of objects as per the key
   * @param {*} items input data - array
   * @param {*} key key for comparison
   */
  sortArryOFObjects(items: any, key: any): any {
    items.sort((a: any, b: any) => {
      return b[key] - a[key];
    });
    return items;
  }

  /**
   * Closes the current opened dialog
   */
  closeModal(data?: any): void {
    this._dialogRef.close(data);
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
